import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { isArrayType, getMDHMFormateTime } from "../../../js/utility.js"

export default {
  name: 'littleassistant',
  data() {
    return {
      p: 1,
      listItem: []
    };
  },
  mounted() {
  },
  computed: {
    title() {
      if (this.$route.params.type === '1') {
        return '小助手';
      } else if (this.$route.params.type === '2') {
        return '提醒助手';
      }
    },
    img_mark() {
      if (this.$route.params.type === '1') {
        return require('../../../assets/zhushou.png');
      } else if (this.$route.params.type === '2') {
        return require('../../../assets/tx.png');
      }
    }
  },
  methods: {
    infinite(done) {
      this.getList(done);
    },
    refresh(done) {
      this.p = 1;
      this.getList(done);
    },
    getList(done) {
      var byName = '';
      if (this.$route.params.type === '1') {
        byName = 'index';
      } else if (this.$route.params.type === '2') {
        byName = 'remind';
      }
      AjaxSilentHttp('POST', 'm=message&a=index', { by: byName, p: this.p }, this, function(res, wself, success) {
        if (success && isArrayType(res.list)) {
          var newItems = [];
          for (var i = 0; i < res.list.length; i++) {
            var item = res.list[i];
            if (wself.$route.params.type === '1') {
              item.time = getMDHMFormateTime(item.send_time);
            } else if (wself.$route.params.type === '2') {
              item.time = getMDHMFormateTime(item.remind_time);
            }
            newItems.push(item);
          }
          if (wself.p === 1) {
            wself.listItem = newItems;
          } else {
            wself.listItem = wself.listItem.concat(newItems);
          }
          if (typeof done === 'function') {
            if (wself.p === 1 && res.list.length < 10) {
              done(true);
            } else if (res.list.length === 0) {
              done(true);
            } else {
              done();
            }
          }
          wself.p = wself.p + 1;
        }
      });
    }
  },
};




